<template>
  <div class="container">
    <div class="row justify-content-center">
      <div class="card" style="width: 25rem;">
        <div class="card-body">
          <GameplayProgress/>
          <div class="card-body">
            <div class="infos">
              <h4 class="card-title">{{ currentStock.name }}</h4>
              <h6 class="card-subtitle mb-2 text-muted">{{ currentStock.industry }}</h6>
              <div class="infos__fact">{{ currentStock.fact }}</div>
            </div>
            <div class="logo">
              <div v-show="showLogo" class="logo">
                <img @error="logoLoadingError"
                     @load="logoLoaded"
                     :src="imageUrl()"
                     class="rounded logo-img"
                     alt=""/>
              </div>
            </div>
          </div>
          <div class="card-body quiz">
            <p v-if="showQuestion" class="card-text">
              <game-question></game-question>
            </p>
            <p v-if="showAnswer" class="card-text">
              <game-answer :valueInMillion="currentStock.value_in_million"></game-answer>
            </p>
            <p v-if="gameEnded" class="card-text">
              Thanks for playing
            </p>
          </div>
          <div class="card-body button-area">
            <div v-if="showQuestion" class="w-100 btn-group btn-group-lg" role="group">
              <button type="button"
                      class="btn btn-primary"
                      @click="choseLess">Less
              </button>
              <button type="button"
                      class="btn btn-primary"
                      @click="choseMore">More
              </button>
            </div>
            <div v-if="showAnswer" class="w-100 btn-group btn-group-lg" role="group">
              <button type="button"
                      class="btn btn-primary"
                      @click="nextStock">Next Stock
              </button>
            </div>
            <div v-if="gameEnded" class="w-100 btn-group btn-group-lg" role="group">
              <button type="button"
                      class="btn btn-primary"
                      @click="backToCategories">Back to categories
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div v-show="showLogo">
      <a href="https://clearbit.com" class="logo-attribution">Logos provided by Clearbit</a>
    </div>
  </div>
</template>

<script>
  import {mapActions, mapGetters} from "vuex"
  import GameplayProgress from "./GameplayProgress"
  import GameQuestion from "./GameQuestion"
  import GameAnswer from "./GameAnswer"

  export default {
    name: 'Categories',
    components: {GameplayProgress, GameQuestion, GameAnswer},
    data() {
      return {
        showLogo: false,
        gameEnded: false,
        showQuestion: false,
        showAnswer: false
      }
    },
    computed: {
      ...mapGetters(['categoryById', 'currentStock', 'currentStockAnchor', 'stocksLeft', 'choices'])
    },
    methods: {
      ...mapActions(['fetchCategories']),
      logoLoadingError() {
        this.showLogo = false
      },
      logoLoaded() {
        this.showLogo = true
      },
      backToCategories() {
        this.$router.push("/")
      },
      imageUrl() {
        return `https://logo.clearbit.com/${this.currentStock.website}?size=500`
      },
      nextStock() {
        this.$store.dispatch('nextStock')
        this.showAnswer = false
        this.showQuestion = true
      },
      _afterChoosing() {
        this.showAnswer = true
        if (this.choices.length === 10) {
          this.gameEnded = true
          this.showAnswer = false
        }
        this.showQuestion = false
      },
      choseLess() {
        if (this.currentStock.value < this.currentStockAnchor) {
          this.$store.dispatch('addChoice', true)
        } else {
          this.$store.dispatch('addChoice', false)
        }
        this._afterChoosing()
      },
      choseMore() {
        if (this.currentStock.value > this.currentStockAnchor) {
          this.$store.dispatch('addChoice', true)
        } else {
          this.$store.dispatch('addChoice', false)
        }
        this._afterChoosing()
      }
    },
    created() {
      this.fetchCategories().then(() => {
        this.$store.dispatch('setCategory', this.$route.params.id)
        this.$store.dispatch('nextStock')
        this.showQuestion = true
      })
    },
    destroyed() {
      this.$store.dispatch('resetGame')
    }
  }
</script>

<style lang="scss" scoped>
  .logo {
    height: 150px;
  }

  .logo-img {
    max-height: 150px;
    max-width: 150px;
  }

  .logo-attribution {
    font-size: 12pt;
  }

  .button-area {
    height: 75px
  }

  .quiz {
    height: 150px;
  }

  .infos {
    height: 300px;

    &__fact {
      font-size: 0.9rem;
    }
  }
</style>
