<template>
  <div class="progress">
    <div class="progress-bar"
         :style="progressWidth()"
         role="progressbar"></div>
  </div>
</template>
<script>
import {mapGetters} from "vuex"

export default {
  name: 'GameplayProgress',
  props: {
    scored: Array
  },
  computed: {
    ...mapGetters(['choices', 'roundsToPlaySetting'])
  },
  methods: {
    progressWidth() {
      let amountOfStocks = this.roundsToPlaySetting
      let progressPercentage = this.choices.length * 100 / amountOfStocks
      return `width: ${Math.round(progressPercentage)}%;`
    },
  }
}
</script>
