<template>
  <div>
    <h5>Market capitalisation of</h5>
    <h4 class="font-weight-bold">{{ currentStockAnchor }} {{ unit }} $</h4>
    <div class="question">
      <h3>less or more?</h3>
    </div>
  </div>
</template>

<script>
  import {mapGetters} from "vuex"

  export default {
    name: "GameQuestion.vue",
    data() {
      return {
        unit: 'billion',
        value: 0
      }
    },
    computed: {
      ...mapGetters(['currentStockAnchor'])
    },
    created() {
      if (this.currentStockAnchor >= 10000) {
        this.unit = 'billion'
      } else {
        this.unit = 'million'
      }
    }
  }
</script>

<style scoped>
  .question {
    margin-top: 25px;
    color: #fe9400;
  }
</style>
