import Vue from 'vue'
import Vuex from 'vuex'
import { db } from '@/firebase'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    stocksToPlay: [],
    currentStock: {},
    choices: [],
    categories: [],
    roundsToPlaySetting: 10
  },
  mutations: {
    addChoice(state, choice) {
      state.choices.push(choice)
    },
    setNextStock(state) {
      state.currentStock = state.stocksToPlay.shift()
    },
    setStocksToPlay(state, stocks) {
      state.stocksToPlay = stocks
    },
    setCategory(state, categoryId) {
      state.stocksToPlay = [...state.categories.find(category => category.id === categoryId).stocks]
    },
    setCategories(state, categories) {
      state.categories = categories
    },
    resetGame(state) {
      state.currentStock = {}
      state.stocksToPlay = []
      state.choices = []
    }
  },
  actions: {
    async fetchCategories({commit}) {
      const dbCategories = await db.collection('categories').get()
      const categories = []
      dbCategories.forEach(doc => {
        categories.push({ id: doc.id.toLocaleLowerCase(), ...doc.data() })
      })
      commit('setCategories', categories)
    },
    addChoice({commit}, choice) {
      commit('addChoice', choice)
    },
    nextStock({commit}) {
      commit('setNextStock')
    },
    setStocksToPlay({commit}, stocks) {
      commit('setStocksToPlay', stocks)
    },
    resetGame({commit}) {
      commit('resetGame')
    },
    setCategory({commit}, category) {
      commit('setCategory', category)
    }
  },
  getters: {
    roundsToPlaySetting: state => {
      return state.roundsToPlaySetting
    },
    currentStock: state => {
      return state.currentStock
    },
    currentStockAnchor: state => {
      const value = state.currentStock.value_in_million
      const addAbove = Math.floor(Math.random() * 2) === 0
      const atLeast10Percent = 0.1
      const percentagePoints = Math.floor(Math.random() * 6) / 100 + atLeast10Percent
      let anchoredValue = 0
      if (addAbove) {
        anchoredValue = value + (value * percentagePoints)
      } else {
        anchoredValue = value - (value * percentagePoints)
      }
      if (value >= 10000) {
        return Math.floor(anchoredValue / 1000)
      } else {
        return Math.floor(anchoredValue)
      }
    },
    stocks: state => {
      return state.stocksToPlay
    },
    stocksLeft: state => {
      return state.stocksToPlay.length
    },
    choices: state => {
      return state.choices
    },
    lastChoice: state => {
      return [...state.choices].pop()
    },
    categories: state => {
      return state.categories
    },
    categoryById: state => id => {
      const amountOfStocksToPlay = this.roundsToPlaySetting()
      return state.categories.find(category => category.id === id)
        .slice(0, amountOfStocksToPlay)
    }
  }
})
