<template>
  <div>
    <h4 class="font-weight-bold">{{ wasCorrect }} {{ value }} {{ unit }} $</h4>
  </div>
</template>

<script>
  import {mapGetters} from "vuex"

  export default {
    name: "GameAnswer",
    props: {
      valueInMillion: Number
    },
    data() {
      return {
        value: 0
      }
    },
    computed: {
      ...mapGetters(['lastChoice']),
      wasCorrect() {
        return this.lastChoice ? 'Yes, ' : 'No, '
      }
    },
    created() {
      if (this.valueInMillion>= 10000) {
        this.unit = 'billion'
        this.value = Math.floor(this.valueInMillion / 1000)
      } else {
        this.unit = 'million'
        this.value = this.valueInMillion
      }
    }
  }
</script>

